import * as THREE from 'three';
import { Easing } from './utils.js';

/**
 * CameraController class with the original method names but improved rotation handling
 */
export class CameraController {
    /**
     * Create a camera controller
     * @param {THREE.Camera} camera - The camera to control
     * @param {HTMLElement} domElement - The DOM element to attach listeners to
     */
    constructor(camera, domElement) {
        this.camera = camera;
        this.domElement = domElement;
        
        // Movement state - keeps original structure
        this.moveState = {
            normalSpeed: 0.008,      // Very slow base speed
            sprintSpeed: 0.02,       // Sprint speed when shift is pressed
            moveSpeed: 0.008,        // Current move speed
            keysPressed: {},
            pointerLocked: false,
            velocity: new THREE.Vector3(),
            direction: new THREE.Vector3(),
            mouseSensitivity: 0.001, // Reduced from original
            playerHeight: 1.6
        };
        
        // Camera behavior state - keeps original structure
        this.cameraState = {
            isControlActive: false,
            lastActiveTime: 0,
            idleThreshold: 3000,
            isLookingAtArtwork: false,
            currentArtworkIndex: -1,
            lookAwayTimer: 0,
            lookBackTimer: 0,
            naturalMovementPhase: 0,
            wiggleAmount: 0.00002,    // Very subtle wiggle
            breathingPhase: 0,
            breathingAmount: 0.00001, // Very subtle breathing
            lookSpeed: 0.01,         // Reduced from original
            focusIntensity: 0,
            lookAwayPoint: null
        };
        
        // Animation state - keeps original structure
        this.animationState = {
            introComplete: false,
            pathProgress: 0,
            pathDuration: 15000,
            startTime: null,
            cameraPath: null,
            lookAtPoints: [],
            currentLookIndex: 0
        };
        
        // New properties for improved rotation handling
        this.yaw = 0;   // Horizontal rotation (around Y axis)
        this.pitch = 0; // Vertical rotation (around X axis)
        
        // Extract initial yaw/pitch from camera
        this.extractYawPitch();
        
        // Setup event listeners
        this.setupEventListeners();
    }
    
    /**
     * Extract yaw and pitch from current camera rotation
     */
    extractYawPitch() {
        const euler = new THREE.Euler().setFromQuaternion(this.camera.quaternion, 'YXZ');
        this.yaw = euler.y;
        this.pitch = euler.x;
    }
    
    /**
     * Update camera rotation from yaw and pitch
     * This approach prevents gimbal lock and unwanted roll
     */
    updateCameraRotation() {
        // Reset rotation
        this.camera.rotation.set(0, 0, 0);
        
        // Apply rotations in the correct order
        this.camera.rotateY(this.yaw);     // First rotate around Y (left/right)
        this.camera.rotateX(this.pitch);   // Then rotate around X (up/down)
    }
    
    /**
     * Setup event listeners for user input
     */
    setupEventListeners() {
        // Keyboard controls
        document.addEventListener('keydown', this.onKeyDown.bind(this));
        document.addEventListener('keyup', this.onKeyUp.bind(this));
        
        // Mouse controls
        document.addEventListener('mousemove', this.onMouseMove.bind(this));
        this.domElement.addEventListener('click', this.onMouseClick.bind(this));
        
        // Pointer lock change
        document.addEventListener('pointerlockchange', this.onPointerLockChange.bind(this));
        
        // Handle ESC key to toggle pointer lock
        document.addEventListener('keydown', (event) => {
            if (event.key === 'Escape') {
                if (document.pointerLockElement === this.domElement) {
                    document.exitPointerLock();
                } else if (this.animationState.introComplete) {
                    this.domElement.requestPointerLock();
                }
            }
        });
        
        // Window resize
        window.addEventListener('resize', this.onWindowResize.bind(this));
    }
    
    /**
     * Handle window resize
     */
    onWindowResize() {
        this.camera.aspect = window.innerWidth / window.innerHeight;
        this.camera.updateProjectionMatrix();
    }
    
    /**
     * Handle keydown events
     * @param {KeyboardEvent} event - Keyboard event
     */
    onKeyDown(event) {
        const key = event.key.toLowerCase();
        this.moveState.keysPressed[key] = true;
        
        // Handle shift key for sprint - support both shift keys
        if (key === 'shift') {
            this.moveState.moveSpeed = this.moveState.sprintSpeed;
        }
        
        this.cameraState.lastActiveTime = Date.now();
    }
    
    /**
     * Handle keyup events
     * @param {KeyboardEvent} event - Keyboard event
     */
    onKeyUp(event) {
        const key = event.key.toLowerCase();
        this.moveState.keysPressed[key] = false;
        
        // Reset sprint speed when shift is released
        if (key === 'shift') {
            this.moveState.moveSpeed = this.moveState.normalSpeed;
        }
    }
    
    /**
     * Handle mouse movement
     * @param {MouseEvent} event - Mouse event
     */
    onMouseMove(event) {
        if (this.moveState.pointerLocked) {
            const movementX = event.movementX || event.mozMovementX || event.webkitMovementX || 0;
            const movementY = event.movementY || event.mozMovementY || event.webkitMovementY || 0;
            
            // Update yaw (horizontal) and pitch (vertical) angles
            this.yaw -= movementX * this.moveState.mouseSensitivity;
            this.pitch -= movementY * this.moveState.mouseSensitivity;
            
            // Clamp pitch to prevent camera flipping
            this.pitch = Math.max(-Math.PI/2 + 0.01, Math.min(Math.PI/2 - 0.01, this.pitch));
            
            // Apply rotation - this is the key method that prevents roll
            this.updateCameraRotation();
            
            this.cameraState.lastActiveTime = Date.now();
        }
    }
    
    /**
     * Handle mouse click
     */
    onMouseClick() {
        if (!this.moveState.pointerLocked && this.animationState.introComplete) {
            this.domElement.requestPointerLock();
        }
    }
    
    /**
     * Handle pointer lock change
     */
    onPointerLockChange() {
        this.moveState.pointerLocked = document.pointerLockElement === this.domElement;
    }
    
    /**
     * Setup camera path for guided introduction
     * @param {Array<THREE.Vector3>} pathPoints - Points defining the camera path
     * @param {Array<THREE.Vector3>} lookPoints - Points to look at during the path
     */
    setupCameraPath(pathPoints, lookPoints) {
        this.animationState.cameraPath = new THREE.CatmullRomCurve3(pathPoints);
        this.animationState.cameraPath.tension = 0.4; // Adjust curve tension for smoother motion
        this.animationState.lookAtPoints = lookPoints;
    }
    
    /**
     * Start the introduction animation
     */
    startIntroAnimation() {
        this.animationState.startTime = Date.now();
        this.animationState.introComplete = false;
    }
    
    /**
     * Update the camera along the introduction path
     * @param {number} delta - Time since last update in seconds
     */
    updateCameraPath(delta) {
        const elapsedTime = Date.now() - this.animationState.startTime;
        const rawProgress = Math.min(elapsedTime / this.animationState.pathDuration, 1);
        this.animationState.pathProgress = Easing.easeInOutCubic(rawProgress); // Apply easing
        
        // Update camera position along spline
        const currentPoint = this.animationState.cameraPath.getPointAt(this.animationState.pathProgress);
        this.camera.position.copy(currentPoint);
        
        // Update which point to look at based on progress
        this.animationState.currentLookIndex = Math.floor(this.animationState.pathProgress * this.animationState.lookAtPoints.length);
        this.animationState.currentLookIndex = Math.min(this.animationState.currentLookIndex, this.animationState.lookAtPoints.length - 1);
        
        // Get look-at point
        const lookPoint = this.animationState.lookAtPoints[this.animationState.currentLookIndex];
        
        // Create a look-at matrix
        const lookAtMatrix = new THREE.Matrix4();
        lookAtMatrix.lookAt(this.camera.position, lookPoint, new THREE.Vector3(0, 1, 0));
        
        // Convert to euler angles and extract yaw/pitch
        const euler = new THREE.Euler().setFromRotationMatrix(lookAtMatrix, 'YXZ');
        this.yaw = euler.y;
        this.pitch = euler.x;
        
        // Apply rotation using our roll-preventing method
        this.updateCameraRotation();
        
        // If the animation is complete, set the flag
        if (rawProgress >= 1) {
            this.animationState.introComplete = true;
        }
    }
    
    /**
     * Apply subtle camera wiggle to simulate natural movement
     * @param {number} delta - Time since last update in seconds
     */
    applyCameraWiggle(delta) {
        // Update movement phases
        this.cameraState.naturalMovementPhase += delta * 0.15;
        this.cameraState.breathingPhase += delta * 0.1;
        
        if (this.cameraState.naturalMovementPhase > Math.PI * 2) {
            this.cameraState.naturalMovementPhase -= Math.PI * 2;
        }
        if (this.cameraState.breathingPhase > Math.PI * 2) {
            this.cameraState.breathingPhase -= Math.PI * 2;
        }
        
        // Store original orientation
        const originalYaw = this.yaw;
        const originalPitch = this.pitch;
        
        // Calculate movements - much more subtle than before
        const wiggleX = Math.sin(this.cameraState.naturalMovementPhase) * this.cameraState.wiggleAmount;
        const wiggleY = Math.cos(this.cameraState.naturalMovementPhase * 1.3) * this.cameraState.wiggleAmount * 0.7;
        const breathing = Math.sin(this.cameraState.breathingPhase) * this.cameraState.breathingAmount;
        
        // Apply movements to yaw/pitch
        this.yaw += wiggleX;
        this.pitch += wiggleY + breathing;
        
        // Apply rotation using roll-preventing method
        this.updateCameraRotation();
        
        // Apply subtle position change for breathing
        this.camera.position.y += breathing * 0.05;
        
        // Restore original orientation for next frame
        this.yaw = originalYaw;
        this.pitch = originalPitch;
    }
    
    /**
     * Check if an artwork is in the camera's field of view
     * @param {THREE.Object3D} artwork - The artwork to check
     * @returns {boolean} - True if artwork is in view
     */
    isArtworkInView(artwork) {
        const artworkPosition = artwork.position.clone();
        
        // Get forward direction from camera's current orientation
        const forward = new THREE.Vector3(0, 0, -1);
        forward.applyQuaternion(this.camera.quaternion);
        
        const directionToArtwork = artworkPosition.clone().sub(this.camera.position).normalize();
        const dotProduct = directionToArtwork.dot(forward);
        
        // Check if artwork is in front of camera (within ~60° field of view)
        if (dotProduct > 0.5) {
            // Check if artwork is facing camera (if it has a normal)
            if (artwork.normal) {
                const artworkNormal = artwork.normal.clone();
                const normalDotProduct = artworkNormal.dot(directionToArtwork.negate());
                if (normalDotProduct <= 0.3) return false;
            }
            
            // Calculate distance to determine if we're close enough
            const distance = this.camera.position.distanceTo(artwork.position);
            const viewingDistance = artwork.userData && artwork.userData.viewingDistance ? 
                                   artwork.userData.viewingDistance : 5;
            
            return distance < viewingDistance * 2;
        }
        
        return false;
    }
    
    /**
     * Find artwork that is currently in view
     * @param {Array<THREE.Object3D>} artPieces - Array of artwork objects
     * @returns {THREE.Object3D|null} - The artwork in view, or null if none
     */
    findArtworkInView(artPieces) {
        for (let i = 0; i < artPieces.length; i++) {
            if (this.isArtworkInView(artPieces[i])) {
                return artPieces[i];
            }
        }
        return null;
    }
    
    /**
     * Smoothly look at a target
     * @param {THREE.Vector3} target - Position to look at
     * @param {number} speed - Speed of the look movement
     */
    lookAtSmoothly(target, speed) {
        // Create a look-at matrix
        const lookAtMatrix = new THREE.Matrix4();
        lookAtMatrix.lookAt(this.camera.position, target, new THREE.Vector3(0, 1, 0));
        
        // Convert to euler angles
        const targetEuler = new THREE.Euler().setFromRotationMatrix(lookAtMatrix, 'YXZ');
        
        // Smoothly interpolate current yaw and pitch toward target
        this.yaw = this.yaw + (targetEuler.y - this.yaw) * speed;
        this.pitch = this.pitch + (targetEuler.x - this.pitch) * speed;
        
        // Apply rotations using our roll-preventing method
        this.updateCameraRotation();
    }
    
    /**
     * Update natural looking behavior
     * @param {number} delta - Time since last update in seconds
     * @param {Array<THREE.Object3D>} artPieces - Array of artwork objects
     * @param {Function} onFocusArtwork - Callback when focusing on artwork
     * @param {Function} onBlurArtwork - Callback when looking away from artwork
     */
    updateNaturalLooking(delta, artPieces, onFocusArtwork, onBlurArtwork) {
        const currentTime = Date.now();
        const idleTime = currentTime - this.cameraState.lastActiveTime;
        
        if (idleTime < this.cameraState.idleThreshold) {
            // Not idle enough yet
            if (this.cameraState.isLookingAtArtwork) {
                this.cameraState.isLookingAtArtwork = false;
                if (onBlurArtwork) onBlurArtwork();
            }
            return;
        }
        
        // Apply the subtle wiggle
        this.applyCameraWiggle(delta);
        
        // Artwork focus behavior
        if (!this.cameraState.isLookingAtArtwork) {
            const artworkInView = this.findArtworkInView(artPieces);
            
            if (artworkInView) {
                this.cameraState.isLookingAtArtwork = true;
                this.cameraState.currentArtworkIndex = artworkInView.userData.index;
                this.cameraState.lookAwayTimer = 4 + Math.random() * 3; // 4-7 seconds until look away
                
                // Call callback for focusing on artwork
                if (onFocusArtwork) onFocusArtwork(artworkInView);
            }
        } 
        else {
            // We're already focused on an artwork
            const currentArtwork = artPieces[this.cameraState.currentArtworkIndex];
            
            // Check if artwork still exists
            if (!currentArtwork) {
                this.cameraState.isLookingAtArtwork = false;
                if (onBlurArtwork) onBlurArtwork();
                return;
            }
            
            // Check if we should look away temporarily
            this.cameraState.lookAwayTimer -= delta;
            
            if (this.cameraState.lookAwayTimer <= 0 && this.cameraState.lookBackTimer <= 0) {
                // Look slightly away from the artwork
                const lookAwayPoint = currentArtwork.position.clone();
                
                // Add random offset
                lookAwayPoint.x += (Math.random() - 0.5) * 1.0;
                lookAwayPoint.y += (Math.random() - 0.5) * 0.5;
                lookAwayPoint.z += (Math.random() - 0.5) * 0.3;
                
                this.cameraState.lookAwayPoint = lookAwayPoint;
                this.cameraState.lookBackTimer = 1 + Math.random() * 1.5; // 1-2.5 seconds before looking back
            }
            else if (this.cameraState.lookBackTimer > 0) {
                // Looking away phase
                this.cameraState.lookBackTimer -= delta;
                
                // Look at the away point
                this.lookAtSmoothly(this.cameraState.lookAwayPoint, this.cameraState.lookSpeed * delta);
                
                if (this.cameraState.lookBackTimer <= 0) {
                    // Time to look back at the artwork
                    this.cameraState.lookAwayTimer = 3 + Math.random() * 4; // 3-7 seconds before looking away again
                }
            }
            else {
                // Looking at artwork phase
                this.lookAtSmoothly(currentArtwork.position, this.cameraState.lookSpeed * delta);
            }
            
            // Check if artwork is still in view
            if (!this.isArtworkInView(currentArtwork)) {
                this.cameraState.isLookingAtArtwork = false;
                this.cameraState.currentArtworkIndex = -1;
                if (onBlurArtwork) onBlurArtwork();
            }
        }
    }
    
    /**
     * Move the camera based on keyboard input
     * @param {number} delta - Time since last update in seconds
     * @param {Function} collisionCheck - Callback to check for collisions
     */
    moveCamera(delta, collisionCheck) {
        // Only allow movement if pointer is locked or intro is complete
        if (!this.moveState.pointerLocked && this.animationState.introComplete) return;
        
        // Check for sprint key (shift)
        if (this.moveState.keysPressed['shift']) {
            this.moveState.moveSpeed = this.moveState.sprintSpeed;
        } else {
            this.moveState.moveSpeed = this.moveState.normalSpeed;
        }
        
        const speed = this.moveState.moveSpeed * delta * 100;
        
        // Reset velocity
        this.moveState.velocity.set(0, 0, 0);
        this.moveState.direction.set(0, 0, 0);
        
        // Calculate movement direction in local space
        if (this.moveState.keysPressed['w']) this.moveState.direction.z = -1;
        if (this.moveState.keysPressed['s']) this.moveState.direction.z = 1;
        if (this.moveState.keysPressed['a']) this.moveState.direction.x = -1;
        if (this.moveState.keysPressed['d']) this.moveState.direction.x = 1;
        
        // Normalize direction vector to maintain consistent speed when moving diagonally
        if (this.moveState.direction.length() > 0) {
            this.moveState.direction.normalize();
        }
        
        // Convert direction from local space to world space
        // IMPORTANT: Only use the yaw rotation for movement
        const rotationMatrix = new THREE.Matrix4();
        rotationMatrix.makeRotationY(this.yaw);
        this.moveState.direction.applyMatrix4(rotationMatrix);
        
        // Apply movement
        this.moveState.velocity.x = this.moveState.direction.x * speed;
        this.moveState.velocity.z = this.moveState.direction.z * speed;
        
        // Store original position for collision detection
        const originalPosition = this.camera.position.clone();
        
        // Apply velocity to camera
        this.camera.position.x += this.moveState.velocity.x;
        this.camera.position.z += this.moveState.velocity.z;
        
        // Check for collisions
        if (collisionCheck) {
            const collision = collisionCheck(this.camera.position, this.moveState.playerHeight / 2);
            
            if (collision) {
                // If collision, revert to original position
                this.camera.position.copy(originalPosition);
            }
        }
    }
    
    /**
     * Update the camera controller
     * @param {number} delta - Time since last update in seconds
     * @param {Array<THREE.Object3D>} artPieces - Array of artwork objects
     * @param {Function} collisionCheck - Callback to check for collisions
     * @param {Function} onFocusArtwork - Callback when focusing on artwork
     * @param {Function} onBlurArtwork - Callback when looking away from artwork
     */
    update(delta, artPieces, collisionCheck, onFocusArtwork, onBlurArtwork) {
        // Update animation state based on user activity
        const currentTime = Date.now();
        if (currentTime - this.cameraState.lastActiveTime < this.cameraState.idleThreshold) {
            this.cameraState.isControlActive = true;
        } else {
            this.cameraState.isControlActive = false;
        }
        
        // Handle different camera states
        if (!this.animationState.introComplete) {
            // During introduction, update camera position along the path
            this.updateCameraPath(delta);
        } else if (this.cameraState.isControlActive) {
            // User is actively controlling the camera
            this.moveCamera(delta, collisionCheck);
            
            // Reset natural camera behavior state
            if (this.cameraState.isLookingAtArtwork) {
                this.cameraState.isLookingAtArtwork = false;
                if (onBlurArtwork) onBlurArtwork();
            }
        } else {
            // User is idle, apply natural camera movements
            this.updateNaturalLooking(delta, artPieces, onFocusArtwork, onBlurArtwork);
        }
    }
}