import * as THREE from 'three';
import '../css/style.css';
import { AssetLoader } from './assetLoader.js';
import { GalleryEnvironment } from './galleryEnvironment.js';
import { CameraController } from './cameraController.js';
import { ArtworkManager } from './artworkManager.js';
import { UI } from './utils.js';

// Main app class
class ArtPortfolioApp {
    constructor() {
        // Initialize properties
        this.scene = null;
        this.camera = null;
        this.renderer = null;
        this.clock = null;
        this.loadingManager = null;
        
        // DOM elements
        this.loadingScreen = null;
        this.progressBar = null;
        this.startButtonContainer = null;
        this.canvasContainer = null;
        this.artworkInfo = null;
        
        // Component classes
        this.assetLoader = null;
        this.galleryEnvironment = null;
        this.cameraController = null;
        this.artworkManager = null;
        
        // Scene objects
        this.walls = [];
        this.artPieces = [];
        
        // Initialize the application
        this.init();
    }
    
    /**
     * Initialize the application
     */
    init() {
        // Setup DOM elements
        this.canvasContainer = document.getElementById('canvas-container');
        
        // Setup loading screen
        this.setupLoadingScreen();
        
        // Initialize Three.js components
        this.initScene();
        this.initCamera();
        this.initRenderer();
        
        // Create asset loader
        this.assetLoader = new AssetLoader(this.loadingManager);
        
        // Create gallery environment
        this.galleryEnvironment = new GalleryEnvironment(this.scene);
        this.walls = this.galleryEnvironment.createEnvironment();
        
        // Create camera controller with reduced speed
        this.cameraController = new CameraController(this.camera, this.renderer.domElement);
        
        // Setup camera path for intro animation
        const cameraPathPoints = [
            new THREE.Vector3(0, 1.6, 10),       // Starting position
            new THREE.Vector3(-3, 1.6, 5),       // Move toward left wall
            new THREE.Vector3(-5, 1.6, 0),       // View left wall artwork
            new THREE.Vector3(0, 1.6, -3),       // Move to center of gallery
            new THREE.Vector3(5, 1.6, 0),        // View right wall artwork
            new THREE.Vector3(0, 1.6, 3)         // Final position in center
        ];
        
        const lookAtPoints = [
            new THREE.Vector3(-5, 1.6, -5),     // Left wall art
            new THREE.Vector3(0, 1.6, -7),      // Back wall art
            new THREE.Vector3(5, 1.6, -5)       // Right wall art
        ];
        
        this.cameraController.setupCameraPath(cameraPathPoints, lookAtPoints);
        
        // Create artwork manager and load artwork immediately
        this.artworkManager = new ArtworkManager(this.scene, this.assetLoader);
        this.loadArtwork();
        
        // Start animation loop
        this.clock = new THREE.Clock();
        this.animate();
    }
    
    /**
     * Setup loading screen
     */
    setupLoadingScreen() {
        this.loadingScreen = document.getElementById('loading-screen');
        this.loadingTitle = document.getElementById('loading-title');
        
        // Setup glitchy VHS-style figlet text
        this.setupGlitchyFigletText();
        
        // Force initial reveal of at least one line
        this.updateGlitchyText(0.1);
        
        this.loadingManager = new THREE.LoadingManager();
        
        this.loadingManager.onProgress = (url, loaded, total) => {
            console.log(`Loading: ${url} - ${loaded}/${total}`);
            
            // Update glitchy text based on loading progress
            const progress = loaded / total;
            this.updateGlitchyText(progress);
        };
        
        this.loadingManager.onError = (url) => {
            console.error(`Error loading: ${url}`);
            // Start experience even if loading fails
            this.startExperience();
        };
        
        this.loadingManager.onLoad = () => {
            console.log('All resources loaded successfully');
            this.updateGlitchyText(1.0); // Ensure all lines are shown
            
            // Small delay before starting experience to appreciate the full text
            setTimeout(() => {
                this.startExperience();
            }, 1000);
        };
        
        // Simulate progressive loading if no assets are being loaded
        let simulatedProgress = 0.1;
        this.progressInterval = setInterval(() => {
            simulatedProgress += 0.1;
            if (simulatedProgress <= 1.0) {
                this.updateGlitchyText(simulatedProgress);
            } else {
                clearInterval(this.progressInterval);
            }
        }, 500);
        
        // Force starting experience after a timeout even if loading doesn't complete
        setTimeout(() => {
            clearInterval(this.progressInterval);
            document.getElementById('loading-message').textContent = 'Starting experience...';
            this.updateGlitchyText(1.0); // Show all lines
            this.startExperience();
        }, 5000); // Give a bit more time to see the glitchy text
    }
    
    /**
     * Setup glitchy figlet ASCII art text
     */
    setupGlitchyFigletText() {
        // The figlet ASCII art text for "akadolypse"
        const figletLines = [
            "       _             _       _                      ",
            "  __ _| | ____ _  __| | ___ | |_   _ _ __  ___  ___ ",
            " / _` | |/ / _` |/ _` |/ _ \\| | | | | '_ \\/ __|/ _ \\",
            "| (_| |   < (_| | (_| | (_) | | |_| | |_) \\__ \\  __/",
            " \\__,_|_|\\_\\__,_|\\__,_|\\___/|_|\\__, | .__/|___/\\___|",
            "                               |___/|_|             "
        ];
        
        // Store the lines for use in animation
        this.figletLines = figletLines;
        this.revealedLines = 0;
        
        // Clear existing content
        this.loadingTitle.innerHTML = '';
        
        // Create wrapper div with glitch text effect
        this.loadingTitle.setAttribute('data-text', figletLines.join('\n'));
        this.loadingTitle.className = 'glitch-text horizontal-glitch';
        
        // Initially the content is empty - will be added line by line
        this.loadingTitle.textContent = '';
    }
    
    /**
     * Update glitchy figlet text based on loading progress
     * @param {number} progress - Loading progress from 0 to 1
     */
    updateGlitchyText(progress) {
        // Calculate how many lines should be visible based on progress
        const lineCount = this.figletLines.length;
        const linesToShow = Math.max(1, Math.ceil(progress * lineCount));
        
        if (linesToShow > this.revealedLines || linesToShow < this.revealedLines) {
            console.log(`Updating glitchy text: ${linesToShow}/${lineCount} lines`);
            
            // Build text with all revealed lines
            const visibleLines = this.figletLines.slice(0, linesToShow);
            const textContent = visibleLines.join('\n');
            
            // Update the text content
            this.loadingTitle.textContent = textContent;
            
            // Also update the data-text attribute for the glitch effect
            this.loadingTitle.setAttribute('data-text', textContent);
            
            // Add random glitches
            if (Math.random() < 0.3) {
                this.addRandomGlitch();
            }
            
            this.revealedLines = linesToShow;
        }
    }
    
    /**
     * Add random glitch effect
     */
    addRandomGlitch() {
        // Temporarily intensify glitch effect
        this.loadingTitle.style.animation = 'none';
        
        // Create a corrupted version of the current text
        const currentText = this.loadingTitle.textContent;
        let glitchedText = currentText;
        
        // Choose a random line to corrupt
        const lines = currentText.split('\n');
        if (lines.length > 0) {
            const randomLineIndex = Math.floor(Math.random() * lines.length);
            
            // Corrupt characters in the line
            let corruptedLine = '';
            const originalLine = lines[randomLineIndex];
            
            for (let i = 0; i < originalLine.length; i++) {
                if (Math.random() < 0.1) {
                    // Replace with a random glitchy character
                    const glitchChars = '_/\\|=-+*%$#@!?';
                    corruptedLine += glitchChars.charAt(Math.floor(Math.random() * glitchChars.length));
                } else {
                    corruptedLine += originalLine.charAt(i);
                }
            }
            
            // Replace the line in the array
            lines[randomLineIndex] = corruptedLine;
            glitchedText = lines.join('\n');
        }
        
        // Apply the glitched text briefly
        this.loadingTitle.textContent = glitchedText;
        
        // Reset after a brief moment
        setTimeout(() => {
            // Restore original text
            this.loadingTitle.textContent = currentText;
            
            // Restart animation
            void this.loadingTitle.offsetWidth; // Trigger reflow
            this.loadingTitle.style.animation = '';
        }, 50 + Math.random() * 200);
    }
    
    /**
     * Start the gallery experience
     */
    startExperience() {
        // Prevent multiple calls to startExperience
        if (this.experienceStarted) {
            return;
        }
        
        this.experienceStarted = true;
        
        // Clean up the progress interval if it's still running
        if (this.progressInterval) {
            clearInterval(this.progressInterval);
            this.progressInterval = null;
        }
        
        this.loadingScreen.style.opacity = '0';
        setTimeout(() => {
            this.loadingScreen.style.display = 'none';
        }, 500);
        
        // Start intro camera path
        this.cameraController.startIntroAnimation();
    }
    
    /**
     * Initialize Three.js scene
     */
    initScene() {
        this.scene = new THREE.Scene();
        this.scene.background = new THREE.Color(0x111111);
    }
    
    /**
     * Initialize camera
     */
    initCamera() {
        this.camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
        this.camera.position.set(0, 1.6, 10);
        this.camera.lookAt(0, 1.6, 0);
    }
    
    /**
     * Initialize renderer
     */
    initRenderer() {
        this.renderer = new THREE.WebGLRenderer({ antialias: true });
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.shadowMap.enabled = true;
        this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
        this.canvasContainer.appendChild(this.renderer.domElement);
    }
    
    /**
     * Load artwork into the gallery
     */
    async loadArtwork() {
        // Get sample artwork data
        const artworkData = this.artworkManager.getSampleArtworkData();
        
        // Load artwork
        this.artPieces = await this.artworkManager.loadArtwork(artworkData);
    }
    
    /**
     * Show artwork information when focusing on an artwork
     * @param {THREE.Object3D} artwork - The artwork being focused on
     */
    showArtworkInfo(artwork) {
        this.artworkInfo = document.getElementById('artwork-info');
        const titleElement = this.artworkInfo.querySelector('.artwork-title');
        const yearElement = this.artworkInfo.querySelector('.artwork-year');
        const descriptionElement = this.artworkInfo.querySelector('.artwork-description');
        
        titleElement.textContent = artwork.userData.title;
        yearElement.textContent = artwork.userData.year;
        descriptionElement.textContent = artwork.userData.description;
        
        UI.fadeIn(this.artworkInfo);
    }
    
    /**
     * Hide artwork information when looking away
     */
    hideArtworkInfo() {
        if (this.artworkInfo) {
            UI.fadeOut(this.artworkInfo);
        }
    }
    
    /**
     * Check for collisions with walls and artwork
     * @param {THREE.Vector3} position - Position to check
     * @param {number} radius - Collision radius
     * @returns {boolean} - True if collision detected
     */
    checkCollisions(position, radius) {
        // Check wall collisions
        for (const wall of this.walls) {
            const wallBox = new THREE.Box3().setFromObject(wall);
            const wallCenter = new THREE.Vector3();
            wallBox.getCenter(wallCenter);
            
            const closestPoint = new THREE.Vector3().copy(position);
            
            closestPoint.x = Math.max(wallBox.min.x, Math.min(closestPoint.x, wallBox.max.x));
            closestPoint.y = Math.max(wallBox.min.y, Math.min(closestPoint.y, wallBox.max.y));
            closestPoint.z = Math.max(wallBox.min.z, Math.min(closestPoint.z, wallBox.max.z));
            
            const distance = closestPoint.distanceTo(position);
            
            if (distance < radius) {
                return true;
            }
        }
        
        // Check artwork collisions
        return this.artworkManager.checkCollisions(position, radius);
    }
    
    /**
     * Animation loop
     */
    animate() {
        requestAnimationFrame(this.animate.bind(this));
        
        const delta = this.clock.getDelta();
        
        // Update camera controller
        this.cameraController.update(
            delta,
            this.artPieces,
            this.checkCollisions.bind(this),
            this.showArtworkInfo.bind(this),
            this.hideArtworkInfo.bind(this)
        );
        
        // Render scene
        this.renderer.render(this.scene, this.camera);
    }
}

// Create and initialize the application
document.addEventListener('DOMContentLoaded', () => {
    // Create a single instance of the app
    window.artPortfolioApp = new ArtPortfolioApp();
});
// Scene setup
let scene, camera, renderer, clock;
let loadingManager, loadingScreen, progressBar, startButtonContainer;
let artPieces = [];
let walls = [];

// Camera states
const cameraState = {
    isControlActive: false,
    lastActiveTime: 0,
    idleThreshold: 3000,
    isLookingAtArtwork: false,
    currentArtworkIndex: -1,
    lookAwayTimer: 0,
    lookBackTimer: 0,
    naturalMovementPhase: 0,
    wiggleAmount: 0.0015,
    breathingPhase: 0,
    breathingAmount: 0.0005,
    lookSpeed: 0.02,
    focusIntensity: 0,
    lookAwayPoint: null
};

// Animation states
const animationState = {
    introComplete: false,
    pathProgress: 0,
    pathDuration: 15000,
    startTime: null,
    lookAtPoints: [],
    currentLookIndex: 0
};

// Movement controls
const moveState = {
    moveSpeed: 0.1,
    keysPressed: {},
    pointerLocked: false,
    velocity: new THREE.Vector3(),
    direction: new THREE.Vector3(),
    mouseSensitivity: 0.002,
    playerHeight: 1.6
};

// DOM elements
const canvasContainer = document.getElementById('canvas-container');

// Initialize the application
init();

function init() {
    // Setup loading screen
    setupLoadingScreen();
    
    // Initialize Three.js components
    initScene();
    initCamera();
    initRenderer();
    
    // Create basic environment
    createEnvironment();
    
    // Setup artwork loading
    loadArtwork();
    
    // Setup event listeners
    setupEventListeners();
    
    // Start animation loop
    clock = new THREE.Clock();
    animate();
}

function setupLoadingScreen() {
    loadingScreen = document.getElementById('loading-screen');
    progressBar = document.getElementById('progress-bar');
    startButtonContainer = document.getElementById('start-button-container');
    
    loadingManager = new THREE.LoadingManager();
    
    loadingManager.onProgress = (url, loaded, total) => {
        const progress = (loaded / total) * 100;
        progressBar.style.width = progress + '%';
    };
    
    loadingManager.onLoad = () => {
        document.getElementById('loading-message').textContent = 'Ready to explore';
        
        const startButton = document.createElement('button');
        startButton.textContent = 'Begin Gallery Tour';
        startButton.addEventListener('click', startExperience);
        startButtonContainer.appendChild(startButton);
    };
}

function startExperience() {
    loadingScreen.style.opacity = '0';
    setTimeout(() => {
        loadingScreen.style.display = 'none';
    }, 500);
    
    // Start intro camera path
    startIntroAnimation();
}

function initScene() {
    scene = new THREE.Scene();
    scene.background = new THREE.Color(0x111111);
    
    // Add ambient and directional lights
    const ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
    scene.add(ambientLight);
    
    const directionalLight = new THREE.DirectionalLight(0xffffff, 0.8);
    directionalLight.position.set(10, 10, 10);
    directionalLight.castShadow = true;
    scene.add(directionalLight);
    
    // Setup shadow properties
    directionalLight.shadow.mapSize.width = 2048;
    directionalLight.shadow.mapSize.height = 2048;
    directionalLight.shadow.camera.near = 0.5;
    directionalLight.shadow.camera.far = 50;
    
    // Add point lights for artwork
    addGalleryLighting();
}

function addGalleryLighting() {
    // Add spotlights for each major wall section
    const spotLightPositions = [
        { pos: new THREE.Vector3(-5, 3, 0), target: new THREE.Vector3(-5, 1.5, 0) },
        { pos: new THREE.Vector3(0, 3, -5), target: new THREE.Vector3(0, 1.5, -5) },
        { pos: new THREE.Vector3(5, 3, 0), target: new THREE.Vector3(5, 1.5, 0) }
    ];
    
    spotLightPositions.forEach(lightData => {
        const spotLight = new THREE.SpotLight(0xffffee, 1);
        spotLight.position.copy(lightData.pos);
        spotLight.target.position.copy(lightData.target);
        spotLight.angle = Math.PI / 6;
        spotLight.penumbra = 0.2;
        spotLight.decay = 2;
        spotLight.distance = 15;
        spotLight.castShadow = true;
        
        scene.add(spotLight);
        scene.add(spotLight.target);
    });
}

function initCamera() {
    camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
    camera.position.set(0, moveState.playerHeight, 10);
    camera.lookAt(0, moveState.playerHeight, 0);
}

function initRenderer() {
    renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.shadowMap.enabled = true;
    renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    canvasContainer.appendChild(renderer.domElement);
}

function createEnvironment() {
    // Create floor
    const floorGeometry = new THREE.PlaneGeometry(30, 30);
    const floorMaterial = new THREE.MeshStandardMaterial({ 
        color: 0x222222, 
        roughness: 0.8 
    });
    const floor = new THREE.Mesh(floorGeometry, floorMaterial);
    floor.rotation.x = -Math.PI / 2;
    floor.position.y = 0;
    floor.receiveShadow = true;
    scene.add(floor);
    
    // Create walls
    createWalls();
    
    // Define camera path points for intro animation
    setupCameraPath();
}

function createWalls() {
    const wallHeight = 4;
    const wallMaterial = new THREE.MeshStandardMaterial({ 
        color: 0xeeeeee, 
        roughness: 0.7 
    });
    
    // Gallery layout - simple room with three wall sections
    const wallSegments = [
        // Back wall
        { pos: new THREE.Vector3(0, wallHeight/2, -10), size: new THREE.Vector3(20, wallHeight, 0.2), rot: new THREE.Vector3(0, 0, 0) },
        // Left wall
        { pos: new THREE.Vector3(-10, wallHeight/2, 0), size: new THREE.Vector3(20, wallHeight, 0.2), rot: new THREE.Vector3(0, Math.PI/2, 0) },
        // Right wall
        { pos: new THREE.Vector3(10, wallHeight/2, 0), size: new THREE.Vector3(20, wallHeight, 0.2), rot: new THREE.Vector3(0, -Math.PI/2, 0) }
    ];
    
    wallSegments.forEach(segment => {
        const wallGeometry = new THREE.BoxGeometry(segment.size.x, segment.size.y, segment.size.z);
        const wall = new THREE.Mesh(wallGeometry, wallMaterial);
        
        wall.position.copy(segment.pos);
        wall.rotation.setFromVector3(segment.rot);
        wall.receiveShadow = true;
        
        scene.add(wall);
        walls.push(wall);
    });
}

function setupCameraPath() {
    // Define path points for camera intro animation
    const cameraPathPoints = [
        new THREE.Vector3(0, moveState.playerHeight, 10),       // Starting position
        new THREE.Vector3(-3, moveState.playerHeight, 5),       // Move toward left wall
        new THREE.Vector3(-5, moveState.playerHeight, 0),       // View left wall artwork
        new THREE.Vector3(0, moveState.playerHeight, -3),       // Move to center of gallery
        new THREE.Vector3(5, moveState.playerHeight, 0),        // View right wall artwork
        new THREE.Vector3(0, moveState.playerHeight, 3)         // Final position in center
    ];
    
    animationState.cameraPath = new THREE.CatmullRomCurve3(cameraPathPoints);
    animationState.cameraPath.tension = 0.4; // Adjust curve tension for smoother motion
    
    // Define look at points during the intro
    animationState.lookAtPoints = [
        new THREE.Vector3(-5, moveState.playerHeight, -5),     // Left wall art
        new THREE.Vector3(0, moveState.playerHeight, -7),      // Back wall art
        new THREE.Vector3(5, moveState.playerHeight, -5)       // Right wall art
    ];
}

function startIntroAnimation() {
    animationState.startTime = Date.now();
    animationState.introComplete = false;
}

function loadArtwork() {
    const textureLoader = new THREE.TextureLoader(loadingManager);
    
    // Define sample artwork data
    const artworkData = [
        { 
            url: 'placeholder-artwork-1.jpg', 
            position: new THREE.Vector3(-5, 1.6, -5), 
            orientation: 'left',
            metadata: { 
                title: 'Abstract Composition #1', 
                description: 'A dynamic exploration of form and color, representing the chaos of modern life.',
                year: '2023'
            } 
        },
        { 
            url: 'placeholder-artwork-2.jpg', 
            position: new THREE.Vector3(0, 1.6, -7), 
            orientation: 'front',
            metadata: { 
                title: 'Serenity', 
                description: 'Minimalist landscape capturing the essence of tranquility and space.',
                year: '2022'
            } 
        },
        { 
            url: 'placeholder-artwork-3.jpg', 
            position: new THREE.Vector3(5, 1.6, -5), 
            orientation: 'right',
            metadata: { 
                title: 'Urban Fragments', 
                description: 'A collage of city elements showing the relationship between structure and humanity.',
                year: '2024'
            } 
        }
    ];
    
    // Create placeholder textures for now
    artworkData.forEach((artwork, index) => {
        // Create a canvas with text for the placeholder
        const canvas = document.createElement('canvas');
        canvas.width = 512;
        canvas.height = 512;
        const context = canvas.getContext('2d');
        
        // Fill background
        context.fillStyle = '#' + Math.floor(Math.random()*16777215).toString(16);
        context.fillRect(0, 0, canvas.width, canvas.height);
        
        // Add text
        context.fillStyle = 'white';
        context.font = 'bold 40px Arial';
        context.textAlign = 'center';
        context.fillText(artwork.metadata.title, canvas.width/2, canvas.height/2);
        
        // Convert canvas to texture
        const texture = new THREE.CanvasTexture(canvas);
        
        // Create artwork plane
        addArtPiece(texture, artwork.position, artwork.orientation, artwork.metadata, index);
    });
}

function addArtPiece(texture, position, orientation, metadata, index) {
    const aspectRatio = 1; // Default to 1:1 for our placeholders
    const width = 2;
    const height = width / aspectRatio;
    
    const artGeometry = new THREE.PlaneGeometry(width, height);
    const artMaterial = new THREE.MeshStandardMaterial({ 
        map: texture,
        side: THREE.DoubleSide
    });
    
    const artPiece = new THREE.Mesh(artGeometry, artMaterial);
    artPiece.position.copy(position);
    
    // Set rotation based on orientation
    if (orientation === 'front') {
        artPiece.rotation.y = 0;
    } else if (orientation === 'left') {
        artPiece.rotation.y = Math.PI / 2;
    } else if (orientation === 'right') {
        artPiece.rotation.y = -Math.PI / 2;
    }
    
    // Store artwork metadata
    artPiece.userData = {
        title: metadata.title,
        description: metadata.description,
        year: metadata.year,
        type: metadata.type || 'painting',
        viewingDistance: metadata.viewingDistance || 2.5,
        viewingDuration: metadata.viewingDuration || { min: 5, max: 10 },
        index: index
    };
    
    // Calculate and store normal vector (for viewing direction)
    artPiece.normal = new THREE.Vector3(0, 0, 1).applyQuaternion(artPiece.quaternion);
    
    artPiece.castShadow = true;
    artPiece.receiveShadow = true;
    
    scene.add(artPiece);
    artPieces.push(artPiece);
    
    return artPiece;
}

function setupEventListeners() {
    // Window resize
    window.addEventListener('resize', onWindowResize);
    
    // Keyboard controls
    document.addEventListener('keydown', onKeyDown);
    document.addEventListener('keyup', onKeyUp);
    
    // Mouse controls
    document.addEventListener('mousemove', onMouseMove);
    document.addEventListener('click', onMouseClick);
    
    // Pointer lock change
    document.addEventListener('pointerlockchange', onPointerLockChange);
    
    // Handle ESC key to toggle pointer lock
    document.addEventListener('keydown', (event) => {
        if (event.key === 'Escape') {
            if (document.pointerLockElement === renderer.domElement) {
                document.exitPointerLock();
            } else {
                renderer.domElement.requestPointerLock();
            }
        }
    });
}

function onWindowResize() {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
}

function onKeyDown(event) {
    moveState.keysPressed[event.key.toLowerCase()] = true;
    cameraState.lastActiveTime = Date.now();
}

function onKeyUp(event) {
    moveState.keysPressed[event.key.toLowerCase()] = false;
}

function onMouseMove(event) {
    if (moveState.pointerLocked) {
        const movementX = event.movementX || event.mozMovementX || event.webkitMovementX || 0;
        const movementY = event.movementY || event.mozMovementY || event.webkitMovementY || 0;
        
        camera.rotation.y -= movementX * moveState.mouseSensitivity;
        
        // Clamp vertical rotation to avoid flipping
        const newRotationX = camera.rotation.x - movementY * moveState.mouseSensitivity;
        camera.rotation.x = Math.max(-Math.PI/2, Math.min(Math.PI/2, newRotationX));
        
        cameraState.lastActiveTime = Date.now();
    }
}

function onMouseClick() {
    if (!moveState.pointerLocked && animationState.introComplete) {
        renderer.domElement.requestPointerLock();
    }
}

function onPointerLockChange() {
    moveState.pointerLocked = document.pointerLockElement === renderer.domElement;
}

function moveCharacter(delta) {
    // Only allow movement if pointer is locked or intro is complete
    if (!moveState.pointerLocked && animationState.introComplete) return;
    
    const speed = moveState.moveSpeed * delta * 100;
    
    // Reset velocity
    moveState.velocity.set(0, 0, 0);
    moveState.direction.set(0, 0, 0);
    
    // Calculate movement direction in local space
    if (moveState.keysPressed['w']) moveState.direction.z = -1;
    if (moveState.keysPressed['s']) moveState.direction.z = 1;
    if (moveState.keysPressed['a']) moveState.direction.x = -1;
    if (moveState.keysPressed['d']) moveState.direction.x = 1;
    
    // Normalize direction vector to maintain consistent speed when moving diagonally
    if (moveState.direction.length() > 0) {
        moveState.direction.normalize();
    }
    
    // Convert direction from local space to world space
    const rotationMatrix = new THREE.Matrix4();
    rotationMatrix.makeRotationY(camera.rotation.y);
    moveState.direction.applyMatrix4(rotationMatrix);
    
    // Apply movement
    moveState.velocity.x = moveState.direction.x * speed;
    moveState.velocity.z = moveState.direction.z * speed;
    
    // Apply velocity to camera
    camera.position.x += moveState.velocity.x;
    camera.position.z += moveState.velocity.z;
    
    // Check collisions after movement
    checkCollisions();
}

function checkCollisions() {
    const playerRadius = 0.5;
    const playerPosition = new THREE.Vector3(
        camera.position.x,
        camera.position.y - moveState.playerHeight / 2, // Check from center of player
        camera.position.z
    );
    
    // Check collisions with walls
    walls.forEach(wall => {
        const wallBox = new THREE.Box3().setFromObject(wall);
        const wallCenter = new THREE.Vector3();
        wallBox.getCenter(wallCenter);
        
        // Simplified collision check - treat player as sphere and wall as box
        const closestPoint = new THREE.Vector3().copy(playerPosition);
        
        closestPoint.x = Math.max(wallBox.min.x, Math.min(closestPoint.x, wallBox.max.x));
        closestPoint.y = Math.max(wallBox.min.y, Math.min(closestPoint.y, wallBox.max.y));
        closestPoint.z = Math.max(wallBox.min.z, Math.min(closestPoint.z, wallBox.max.z));
        
        const distance = closestPoint.distanceTo(playerPosition);
        
        if (distance < playerRadius) {
            // Collision detected, move player back
            const pushDirection = new THREE.Vector3().subVectors(playerPosition, closestPoint).normalize();
            const pushDistance = playerRadius - distance;
            
            camera.position.x += pushDirection.x * pushDistance;
            camera.position.z += pushDirection.z * pushDistance;
        }
    });
    
    // Check collisions with artwork
    artPieces.forEach(art => {
        const artBox = new THREE.Box3().setFromObject(art);
        const artCenter = new THREE.Vector3();
        artBox.getCenter(artCenter);
        
        const closestPoint = new THREE.Vector3().copy(playerPosition);
        
        closestPoint.x = Math.max(artBox.min.x, Math.min(closestPoint.x, artBox.max.x));
        closestPoint.y = Math.max(artBox.min.y, Math.min(closestPoint.y, artBox.max.y));
        closestPoint.z = Math.max(artBox.min.z, Math.min(closestPoint.z, artBox.max.z));
        
        const distance = closestPoint.distanceTo(playerPosition);
        
        if (distance < playerRadius) {
            // Collision detected, move player back
            const pushDirection = new THREE.Vector3().subVectors(playerPosition, closestPoint).normalize();
            const pushDistance = playerRadius - distance;
            
            camera.position.x += pushDirection.x * pushDistance;
            camera.position.z += pushDirection.z * pushDistance;
        }
    });
}

function updateCameraPath(delta) {
    const elapsedTime = Date.now() - animationState.startTime;
    const rawProgress = Math.min(elapsedTime / animationState.pathDuration, 1);
    animationState.pathProgress = easeInOutCubic(rawProgress); // Apply easing
    
    // Update camera position along spline
    const currentPoint = animationState.cameraPath.getPointAt(animationState.pathProgress);
    camera.position.copy(currentPoint);
    
    // Update which point to look at based on progress
    animationState.currentLookIndex = Math.floor(animationState.pathProgress * animationState.lookAtPoints.length);
    animationState.currentLookIndex = Math.min(animationState.currentLookIndex, animationState.lookAtPoints.length - 1);
    
    // Look at appropriate landmark
    camera.lookAt(animationState.lookAtPoints[animationState.currentLookIndex]);
    
    // If the animation is complete, set the flag
    if (rawProgress >= 1) {
        animationState.introComplete = true;
    }
}

// Easing function for smoother acceleration/deceleration
function easeInOutCubic(t) {
    return t < 0.5 ? 4 * t * t * t : 1 - Math.pow(-2 * t + 2, 3) / 2;
}

function applyCameraWiggle(delta) {
    // Update movement phases
    cameraState.naturalMovementPhase += delta * 0.5;
    cameraState.breathingPhase += delta * 0.25;
    
    if (cameraState.naturalMovementPhase > Math.PI * 2) {
        cameraState.naturalMovementPhase -= Math.PI * 2;
    }
    if (cameraState.breathingPhase > Math.PI * 2) {
        cameraState.breathingPhase -= Math.PI * 2;
    }
    
    // Calculate movements
    const wiggleX = Math.sin(cameraState.naturalMovementPhase) * cameraState.wiggleAmount;
    const wiggleY = Math.cos(cameraState.naturalMovementPhase * 1.3) * cameraState.wiggleAmount * 0.7;
    const breathing = Math.sin(cameraState.breathingPhase) * cameraState.breathingAmount;
    
    // Apply movements to camera
    camera.rotation.x += wiggleY + breathing;
    camera.rotation.y += wiggleX;
    camera.position.y += breathing * 0.1;
}

function findArtworkInView() {
    for (let i = 0; i < artPieces.length; i++) {
        if (isArtworkInView(artPieces[i])) {
            return artPieces[i];
        }
    }
    return null;
}

function isArtworkInView(artwork) {
    const artworkPosition = artwork.position.clone();
    const cameraDirection = new THREE.Vector3(0, 0, -1).applyQuaternion(camera.quaternion);
    const directionToArtwork = artworkPosition.clone().sub(camera.position).normalize();
    
    const dotProduct = directionToArtwork.dot(cameraDirection);
    
    // Check if artwork is in front of camera (within ~60° field of view)
    if (dotProduct > 0.5) {
        // Check if artwork is facing camera
        const artworkNormal = artwork.normal || new THREE.Vector3(0, 0, 1).applyQuaternion(artwork.quaternion);
        const normalDotProduct = artworkNormal.dot(directionToArtwork.negate());
        
        // Calculate distance to determine if we're close enough
        const distance = camera.position.distanceTo(artwork.position);
        const isCloseEnough = distance < artwork.userData.viewingDistance * 2;
        
        return normalDotProduct > 0.3 && isCloseEnough;
    }
    
    return false;
}

function updateNaturalLooking(delta) {
    const currentTime = Date.now();
    const idleTime = currentTime - cameraState.lastActiveTime;
    
    if (idleTime < cameraState.idleThreshold) {
        // Not idle enough yet
        hideArtworkInfo();
        return;
    }
    
    // Apply the subtle wiggle
    applyCameraWiggle(delta);
    
    // Artwork focus behavior
    if (!cameraState.isLookingAtArtwork) {
        const artworkInView = findArtworkInView();
        
        if (artworkInView) {
            cameraState.isLookingAtArtwork = true;
            cameraState.currentArtworkIndex = artworkInView.userData.index;
            cameraState.lookAwayTimer = 4 + Math.random() * 3; // 4-7 seconds until look away
            
            // Show artwork info
            showArtworkInfo(artworkInView);
        }
    } 
    else {
        // We're already focused on an artwork
        const currentArtwork = artPieces[cameraState.currentArtworkIndex];
        
        // Check if we should look away temporarily
        cameraState.lookAwayTimer -= delta;
        
        if (cameraState.lookAwayTimer <= 0 && cameraState.lookBackTimer <= 0) {
            // Look slightly away from the artwork
            const lookAwayPoint = currentArtwork.position.clone();
            
            // Add random offset
            lookAwayPoint.x += (Math.random() - 0.5) * 1.0;
            lookAwayPoint.y += (Math.random() - 0.5) * 0.5;
            lookAwayPoint.z += (Math.random() - 0.5) * 0.3;
            
            cameraState.lookAwayPoint = lookAwayPoint;
            cameraState.lookBackTimer = 1 + Math.random() * 1.5; // 1-2.5 seconds before looking back
        }
        else if (cameraState.lookBackTimer > 0) {
            // Looking away phase
            cameraState.lookBackTimer -= delta;
            
            // Look at the away point
            lookAtSmoothly(cameraState.lookAwayPoint, cameraState.lookSpeed * delta);
            
            if (cameraState.lookBackTimer <= 0) {
                // Time to look back at the artwork
                cameraState.lookAwayTimer = 3 + Math.random() * 4; // 3-7 seconds before looking away again
            }
        }
        else {
            // Looking at artwork phase
            lookAtSmoothly(currentArtwork.position, cameraState.lookSpeed * delta);
        }
        
        // Check if artwork is still in view
        if (!isArtworkInView(currentArtwork)) {
            cameraState.isLookingAtArtwork = false;
            cameraState.currentArtworkIndex = -1;
            hideArtworkInfo();
        }
    }
}

function lookAtSmoothly(target, speed) {
    const targetPosition = target.clone();
    
    // Create a temporary camera
    const tempCamera = camera.clone();
    tempCamera.lookAt(targetPosition);
    
    // Smoothly interpolate quaternions
    camera.quaternion.slerp(tempCamera.quaternion, speed);
}

function showArtworkInfo(artwork) {
    const artworkInfo = document.getElementById('artwork-info');
    const titleElement = artworkInfo.querySelector('.artwork-title');
    const yearElement = artworkInfo.querySelector('.artwork-year');
    const descriptionElement = artworkInfo.querySelector('.artwork-description');
    
    titleElement.textContent = artwork.userData.title;
    yearElement.textContent = artwork.userData.year;
    descriptionElement.textContent = artwork.userData.description;
    
    artworkInfo.classList.remove('hidden');
}

function hideArtworkInfo() {
    const artworkInfo = document.getElementById('artwork-info');
    artworkInfo.classList.add('hidden');
}

function animate() {
    requestAnimationFrame(animate);
    
    const delta = clock.getDelta();
    
    // Update animation state based on user activity
    const currentTime = Date.now();
    if (currentTime - cameraState.lastActiveTime < cameraState.idleThreshold) {
        cameraState.isControlActive = true;
    } else {
        cameraState.isControlActive = false;
    }
    
    // Handle different camera states
    if (!animationState.introComplete) {
        // During introduction, update camera position along the path
        updateCameraPath(delta);
    } else if (cameraState.isControlActive) {
        // User is actively controlling the camera
        moveCharacter(delta);
        
        // Reset natural camera behavior state
        cameraState.isLookingAtArtwork = false;
        cameraState.currentArtworkIndex = -1;
    } else {
        // User is idle, apply natural camera movements
        updateNaturalLooking(delta);
    }
    
    renderer.render(scene, camera);
}